import React, { useEffect, useRef, useState } from 'react';
import { SocialIcon } from 'react-social-icons';
// import { motion } from 'framer-motion';
const FooterPage = () => {

  const [isVisible, setIsVisible] = useState(false);
  const elementRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
        else {
          setIsVisible(false);
        }
      });
    }, { threshold: 0.5 }); // Trigger when 50% of the element is in the viewport

    const element = elementRef.current;
    if (element) {
      observer.observe(element);
    }

    // Cleanup observer on component unmount
    return () => {
      if (element) {
        observer.unobserve(element);
      }
    };
  }, [isVisible]);
  const styles = {
    content: {
      textAlign: 'right',
      marginTop: '100px',
      padding: '20px',
      // backgroundColor: '#f0f0f0',
      // border: '1px solid #ccc',
      // boxShadow: '0 2px 8px rgba(0, 0, 0, 0.2)',
      transition: 'transform 0.9s ease-out, opacity 1s ease-out',
      opacity: isVisible ? 100 : 0,
      transform: isVisible ? 'translateY(0px)' : 'translateY(-100px)', // Initially positioned above
    },
  };
  const contactPageStyle = {
    fontFamily: 'Arial, sans-serif',
    // padding: '20px',
  };

  const contactHeaderStyle = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    marginBottom: '20px',
  };

  const contactImageStyle = {
    maxWidth: '200px',
    height: 'auto',
    borderRadius: '10px',
  };

  const contactInfoStyle = {
    textAlign: 'right'
  };

  const contactInfoHeadingStyle = {
    fontSize: '2rem',
    color: '#333',
  };

  const contactInfoSubHeadingStyle = {
    fontSize: '1.2rem',
    color: '#f5a623',
  };

  const companyDetailsStyle = {
    marginTop: '20px',
  };

  const companyDetailsHeadingStyle = {
    fontSize: '1.8rem',
    color: '#333',
  };

  const companyDetailsTextStyle = {
    fontSize: '1.5rem',
    color: 'white',
    fontWeight: "bold",
    textAlign: "center",
    width: "70%",
    alignSelf: "center",
    marginTop: "30px"
  };
  const companyDetailsTextStyle1 = {
    fontSize: '1rem',
    color: 'white',
    textAlign: "center",
    width: "70%",
    alignSelf: "center",
    marginTop: "30px",
    lineHeight: "2"
  };

  const contactFooterStyle = {
    marginTop: '40px',
    height: "280px",
    backgroundColor: "#363636",
    display: "flex",
    flexDirection: "column"
  };

  const footerInfoStyle = {
    fontFamily: "Poppins",
    fontWeight: "400px",
    fontize: "0.875rem",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    color: 'black',
    gap: "40px"
  };

  const socialLinksStyle = {
    display: 'flex',
    alignItems: 'center',
    gap: "10px",
    alignSelf: "center"
  };

  const socialLinkStyle = {
    marginRight: '10px',
    textDecoration: 'none',
    color: '#555',
  };

  const socialLinkHoverStyle = {
    color: '#f5a623',
  };

  return (
    <div style={contactPageStyle}>
      <div style={contactHeaderStyle}>
        <div style={contactImageStyle}>
          <img
            src="https://picsum.photos/seed/picsum/200/300"
            alt="VR Arcade"
            style={contactImageStyle}
          />
        </div>
        <div
          ref={elementRef}
          style={{ marginTop: "40px" }}>

          <h1 style={contactInfoHeadingStyle}>CONTACT US</h1>
          <p style={contactInfoSubHeadingStyle}>Engage, Educate, Entertain!</p>
        </div>
        <div style={footerInfoStyle}>
          <p><strong>Company Number:</strong> <br /><br />+971-521861400</p>
          <p><strong>Email:</strong> <br /><br /><a href="mailto:info@thearcadeacademy.com">info@thearcadeacademy.com</a></p>
          <p><strong>Site:</strong> <br /><br />Office 15, Al-Khabaisi, 29a street, Dubai</p>
        </div>
      </div>
      <div style={contactFooterStyle}>
        <h2 style={companyDetailsTextStyle}>The VR Spaces</h2>

        <p style={companyDetailsTextStyle1}>
          "The Arcade Academy transforms learning into an adventure with engaging VR, 3D and 2D games. Designed for students, parents and schools, our platform makes education fun and accessible anytime and anywhere."
        </p>
        <div style={socialLinksStyle}>
          <SocialIcon network='facebook' url="#" />
          <SocialIcon network='twitter' url="#" />
          <SocialIcon network='google' url="#" />
          <SocialIcon network='linkedin' url="#" />
        </div>
      </div>
    </div >
  );
};

export default FooterPage;
