import React, { useRef, useState, useEffect } from 'react';
import { FaChevronLeft, FaChevronRight } from 'react-icons/fa';

const TeamMembers = () => {
  const teamMembers = [
    {
      name: "Mumshad Jawad",
      role: "Co-Founder & CEO",
      description: "Leads with a passion for blending education and gaming.",
      image: `${process.env.PUBLIC_URL}/myImages/momi_hero.png`, // Replace with actual image URL
    },
    {
      name: "Muhammad Bilal Akram",
      role: "Co-Founder & CTO",
      description: "Oversees product development with creativity and insight.Oversees product development with creativity and insight",
      image: `${process.env.PUBLIC_URL}/myImages/muhammad_bilal.png`, // Replace with actual image URL
    },
    {
      name: "Rashid Nawaz",
      role: "Product Manager",
      description: "Builds robust and scalable software solutions.",
      image: `${process.env.PUBLIC_URL}/myImages/rashid.jpg`, // Replace with actual image URL
    },
  ];

  const sliderRef = useRef(null);
  const [showButtons, setShowButtons] = useState(false);

  const scroll = (direction) => {
    const slider = sliderRef.current;
    const scrollAmount = 300; // Change this value for more or less scroll per click

    if (direction === "left") {
      slider.scrollLeft -= scrollAmount;
    } else {
      slider.scrollLeft += scrollAmount;
    }
  };

  useEffect(() => {
    // Function to check if the slider content overflows
    const checkOverflow = () => {
      const slider = sliderRef.current;
      if (slider.scrollWidth > slider.clientWidth) {
        setShowButtons(true); // Show buttons if content is overflowing
      } else {
        setShowButtons(false); // Hide buttons if content fits within the container
      }
    };

    // Check overflow when component mounts
    checkOverflow();

    // Add resize listener to handle window resizing
    window.addEventListener('resize', checkOverflow);

    // Cleanup listener on component unmount
    return () => {
      window.removeEventListener('resize', checkOverflow);
    };
  }, []);

  return (
    <div style={{ textAlign: "center", padding: "20px", fontFamily: "Arial, sans-serif" }}>
      <div style={{ fontSize: "24px", fontWeight: "bold", marginBottom: "20px", color: "#FFC107" }}>
        OUR TEAM MEMBERS
      </div>
      <br />
      <br />
      <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
        {/* Left Arrow */}
        {showButtons && (
          <button
            onClick={() => scroll("left")}
            style={{
              backgroundColor: "#FFC107",
              border: "none",
              borderRadius: "50%",
              padding: "10px",
              cursor: "pointer",
              marginRight: "10px",
              fontSize: "18px",
              color: "#fff",
            }}
          >
            <FaChevronLeft />
          </button>
        )}

        {/* Team Member Slider */}
        <div
          ref={sliderRef}
          style={{
            display: "flex",
            justifyContent: "center",
            overflowX: "auto",
            gap: "20px",
            padding: "10px",
            scrollBehavior: "smooth",
            width: "100%",
          }}
        >
          {teamMembers.map((member, index) => (
            <div
              key={index}
              style={{
                flex: "0 0 auto",
                width: "300px",
                padding: "20px",
                textAlign: "center",
                borderRadius: "10px",
                boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.5)", // Enhanced shadow
                transition: "transform 0.3s ease, box-shadow 0.3s ease", // Smooth hover effect
                cursor: "pointer",
                // boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
              }}
            >
              <img
                src={member.image}
                alt={member.name}
                style={{
                  borderRadius: "50%",
                  width: "120px",
                  height: "120px",
                  objectFit: "cover",
                  marginTop: "10px",
                  border: "2px solid blue",
                  marginBottom: "10px",
                }}
              />
              <div
                style={{
                  fontSize: "24px",
                  fontWeight: "bold",
                  marginBottom: "10px",
                }}
              >
                {member.name}
              </div>
              <div
                style={{
                  fontSize: "18px",
                  marginBottom: "16px",
                  fontWeight: "bold"
                }}
              >
                {member.role}
              </div>
              <div
                style={{
                  marginBottom: "16px",
                  fontSize: "14px",
                  color: "#555",
                  lineHeight: "2.5"
                }}
              >
                {member.description}
              </div>
            </div>
          ))}
        </div>

        {/* Right Arrow */}
        {showButtons && (
          <button
            onClick={() => scroll("right")}
            style={{
              backgroundColor: "#FFC107",
              border: "none",
              borderRadius: "50%",
              padding: "10px",
              cursor: "pointer",
              marginLeft: "10px",
              fontSize: "18px",
              color: "#fff",
            }}
          >
            <FaChevronRight />
          </button>
        )}
      </div>
    </div>
  );
};

export default TeamMembers;
